import React, {ChangeEvent, KeyboardEvent, useEffect, useRef, useState} from 'react';
import './style.css';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import {
    AUTH_PATH,
    BOARD_DETAIL_PATH, BOARD_PATH,
    BOARD_UPDATE_PATH,
    BOARD_WRITE_PATH,
    MAIN_PATH,
    SEARCH_PATH,
    USER_PATH
} from '../../constants';
import {useCookies} from 'react-cookie';
import {useBoardStore, useEditorStore, useLoginUserStore} from '../../stores';
import {fileUploadRequest, patchBoardRequest, postBoardRequest} from '../../apis';
import {PatchBoardRequestDto, PostBoardRequestDto} from '../../apis/request/board';
import {PatchBoardResponseDto, PostBoardResponseDto} from '../../apis/response/board';
import {ResponseDto} from '../../apis/response';
import {ContentBlock, convertToRaw, EditorState} from 'draft-js';
import {ImageUrl} from "../../types/interface";

//  component:   //
export default function Header() {

    //  state:    //
    const { loginUser, setLoginUser, resetLoginUser } = useLoginUserStore();
    //  state: path  //
    const { pathname } = useLocation();
    //  state: cookie  //
    const [cookies, setCookie] = useCookies();
    //  state:   //
    const [isLogin, setLogin] = useState<boolean>(false);

    const isAuthPage = pathname.startsWith(AUTH_PATH());
    const isMainPage = pathname === MAIN_PATH();
    const isSearchPage = pathname.startsWith(SEARCH_PATH(''));
    const isBoardDetailPage = pathname.startsWith(BOARD_PATH() + '/' + BOARD_DETAIL_PATH(''));
    const isBoardWritePage = pathname.startsWith(BOARD_PATH() + '/' + BOARD_WRITE_PATH());
    const isBoardUpdatePage = pathname.startsWith(BOARD_PATH() + '/' + BOARD_UPDATE_PATH(''));
    const isUserPage = pathname.startsWith(USER_PATH(''));

    //  function:   //
    const navigate = useNavigate();

    //  event handler:      //
    const onLogoClickHandler = () => {
        navigate(MAIN_PATH());
    }

    //  component:    //
    const SearchButton = () => {
        //  state:      //
        const searchButtonRef = useRef<HTMLDivElement | null>(null);
        //  state:    //
        const [status, setStatus] = useState<boolean>(false);
        //  state:   //
        const [word, setWord] = useState<string>('');
        //  state:  path variable  //
        const { searchWord } = useParams();

        //  event handler:      //
        const onSearchWordChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
            const value = event.target.value;
            setWord(value);
        };

        //  event handler:      //
        const onSearchWordKeyDownHandler = (event: KeyboardEvent<HTMLInputElement>) => {
            if (event.key !== 'Enter') return;
            if (!searchButtonRef.current) return;
            searchButtonRef.current.click();
        };

        //  event handler:       //
        const onSearchButtonClickHandler = () => {
            if (!word) {
                alert(' .')
                return;
            }
            if (!status) {
                setStatus(!status);
                return;
            }
            navigate(SEARCH_PATH(word));
        };

        //  effect:  path variable     //
        useEffect(() => {
            if (searchWord) {
                setWord(searchWord);
                setStatus(true);
            }
        }, [searchWord]);
        //  effect: login user     //
        useEffect(() => {
            setLogin(loginUser !== null);
        }, [loginUser])

        // render:     ( true ) //
        return (
            <div className='header-search-input-box'>
                <input className='header-search-input' type='text' placeholder=' .' value={word} onChange={onSearchWordChangeHandler} onKeyDown={onSearchWordKeyDownHandler} />
                <div ref={searchButtonRef} className='icon-button' onClick={onSearchButtonClickHandler}>
                    <div className='icon search-light-icon'></div>
                </div>
            </div>
        );
    }

    //  component:    //
    const MyPageButton = () => {
        //  state: userEmail path variable  //
        const { userEmail } = useParams();

        //  event handler:       //
        const onMyPageButtonClickHandler = () => {
            if (!loginUser) return;
            const { email } = loginUser;
            navigate(USER_PATH(email));
        }
        //  event handler:       //
        const onSignOutButtonClickHandler = () => {
            // eslint-disable-next-line no-restricted-globals
            if (!confirm("  ?")) return;
            resetLoginUser();
            setCookie('accessToken', '', { path: MAIN_PATH(), expires: new Date() });
            setLogin(false);
            navigate(MAIN_PATH());
        }

        //  event handler:       //
        const onSignInButtonClickHandler = () => {
            navigate(AUTH_PATH());
        }

        //  effect:   isLogin true   //
        useEffect(() => {
            //       
            if (!cookies.accessToken) {
                setLogin(false);
            } else {
                setLogin(true);
            }
        }, []);

        //    render:      //
        if(isLogin && (userEmail === loginUser?.email))
        return(
            <div className='black-button' onClick={onSignOutButtonClickHandler}>{''}</div>
        )
        if(isLogin)
        //    render:      //
        return(
            <div className='white-button' onClick={onMyPageButtonClickHandler}>{''}</div>
        )
        //    render:      //
        return(
            <div className='black-button' onClick={onSignInButtonClickHandler}>{''}</div>
        )
    };

    //  component:    //
    const UploadButton = () => {
        //  state:   path variable  //
        const { boardNumber } = useParams();
        //  state:   //
        const { title, content, category, boardImageFileList, resetBoard } = useBoardStore();
        //  state: EditorState  //
        const { editorState, setEditorState } = useEditorStore();

        //  function:   //
        const navigator = useNavigate();

        //  function: post board response   //
        const postBoardResponse = (responseBody: PostBoardResponseDto | ResponseDto | null) => {
            if (!responseBody) return;
            const { code } = responseBody;
            if (code === 'DBE') alert(' .');
            if (code === 'AF' || code === 'NU') navigate(AUTH_PATH());
            if (code === 'VF') alert('  .');
            if (code !== 'SU') return;

            resetBoard();
            if (!loginUser) return;
            const { email } = loginUser;
            navigate(USER_PATH(email));
        }
        //  function: patch board response   //
        const patchBoardResponse = (responseBody: PatchBoardResponseDto | ResponseDto | null) => {
            if (!responseBody) return;
            const { code } = responseBody;
            if (code === 'AF' || code === 'NU' || code == 'NB' || code == 'NP') navigate(AUTH_PATH());
            if (code === 'VF') alert('  .');
            if (code === 'DBE') alert(' .');
            if (code !== 'SU') return;

            if (!boardNumber) return;
            navigate(BOARD_PATH() + '/' + BOARD_DETAIL_PATH(boardNumber));
        }

        //  event handler:       //
        const onUploadButtonClickHandler = async () => {
            // eslint-disable-next-line no-restricted-globals
            if (!confirm("  ?")) return;

            const accessToken = cookies.accessToken;
            if (!accessToken) {
                alert("  .");
                navigator(AUTH_PATH());
                return;
            }

            try {
                //     
                const uploadPromises = boardImageFileList.map((file) => {
                    const data = new FormData();
                    data.append('file', file.file);
                    return fileUploadRequest(data); //   
                });

                //   URL  boardImageList 
                const uploadResults = await Promise.all(uploadPromises);
                const boardImageList = uploadResults.filter(
                    (url): url is string => url !== null
                );

                //  editorState  URL  S3 URL 
                let newEditorState = editorState;
                const contentState = newEditorState.getCurrentContent();
                const blockMap = contentState.getBlockMap();

                let imageIndex = 0;

                blockMap.forEach((block: any) => {
                    const entityKey = block.getEntityAt(0);

                    if (entityKey) {
                        const entity = contentState.getEntity(entityKey);
                        const entityData = entity.getData();

                        if (entity.getType() === 'IMAGE' && boardImageList[imageIndex]) {
                            const newImageUrl = boardImageList[imageIndex]; // index 
                            imageIndex++; //  URL 

                            const newEntityData = { ...entityData, src: newImageUrl };
                            const contentStateWithUpdatedEntity = contentState.replaceEntityData(entityKey, newEntityData);

                            newEditorState = EditorState.push(
                                newEditorState,
                                contentStateWithUpdatedEntity,
                                'apply-entity'
                            );
                        }
                    }
                });

                // JSON 
                const updatedContent = JSON.stringify(
                    convertToRaw(newEditorState.getCurrentContent())
                );

                //       
                const isWritePage = pathname === BOARD_PATH() + '/' + BOARD_WRITE_PATH();
                if (isWritePage) {
                    const requestBody: PostBoardRequestDto = {
                        title,
                        content: updatedContent, //  content 
                        category,
                        boardImageList,
                    };
                    postBoardRequest(requestBody, accessToken).then(postBoardResponse);
                } else {
                    if (!boardNumber) return;
                    const requestBody: PatchBoardRequestDto = {
                        title,
                        content: updatedContent,
                        category,
                        boardImageList,
                    };
                    patchBoardRequest(boardNumber, requestBody, accessToken).then(patchBoardResponse);
                }
            } catch (error) {
                console.error("    :", error);
                alert("    .  .");
            }
        };

        //  render:     //
        if (title && content && category)
        return <div className='black-button' onClick={onUploadButtonClickHandler}>{''}</div>;
        //  render:      //
        return <div className='disable-button'>{''}</div>;
    }
    //  render:    //
    return (
        <div id='header'>
            <div className='header-container'>
                <div className='header-left-box' onClick={onLogoClickHandler}>
                    <div className='icon-box'>
                        <div className='icon logo-dark-icon'></div>
                    </div>
                    <div className='header-logo'>{'DevHub'}</div>
                </div>
                <div className='header-right-box'>
                    {(isMainPage || isSearchPage || isBoardDetailPage) && <SearchButton />}
                    {(isMainPage || isSearchPage || isBoardDetailPage || isUserPage) && <MyPageButton />}
                    {(isBoardWritePage || isBoardUpdatePage) && <UploadButton />}
                </div>
            </div>
        </div>
    )
}